import React from 'react';
import '../../css/home/eventCorner.css';

function EventsCorner() {
    return (
        <div className="eventCorner">
            <div className="eventCorner-heading">
                <h1 className="edjusticeColor">Events&nbsp;</h1><h1>Corner</h1>
            </div>
            <div className="events-content-collection">
                <div className="events-content">
                    <img src="assets/image/home/event-1.jpg" className="events-content-img" />
                    <h2 className="event-content-heading">Welcoming New Volunteers On Board</h2>
                    <div className="event-content-detail">
                        The recruitment drive was quite a buzz in university campuses across Delhi
                        NCR. We finally accepted 25 new volunteers post interviewing all. The
                        formal induction seminar for new volunteers took place on 9th February in Delhi.
                    </div>
                </div>
                <div className="events-content">
                    <img src="assets/image/home/event-2.jpg" className="events-content-img" />
                    <h2 className="event-content-heading">Masterclass by Mr. Randhir Jaiswal</h2>
                    <div className="event-content-detail">
                    We had our first master class with IFS Mr. Randhir Singh Jaiswal at his residence on 29th February. Mr. Jaiswal is a senior Indian diplomat and is currently serving as the Joint Secretary to President of India on Foreign Affairs and would soon be the Counsellor General at New York, USA.
                    </div>
                </div>
                <div className="events-content">
                    <img src="assets/image/home/event-3.jpg" className="events-content-img" />
                    <h2 className="event-content-heading">Virtual Class</h2>
                    <div className="event-content-detail">
                    Women for change .
                    We at Edjustice firmly believe in quality education for all because without equal representation of women in society a nation can't grow .  Dr. Rajni Sharma had a virtual class session with the students of school Begursarai.
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EventsCorner;
