import React from 'react';

const Program1 = () => {
    const contentShow1 = () => {
        var x = document.getElementsByClassName("program1-MoreContent")[0];
        var y = document.getElementsByClassName("program-program1")[0];

        if (x.style.display === "none" || x.style.display === "") {
            x.style.display = "block";
            x.scrollIntoView();
        } else {
            x.style.display = "none";
            y.scrollIntoView();
        }

    }
    return (
        <>
            <div className="program-program1">
                <div className="program1-image">
                    <img src="assets/image/program/program1.webp" />
                    <h2 className="program1-title">Edconnect Mentoring</h2>
                </div>
                <div className="program1-content">
                    <p>The Mentor- Mentee Challenge is an initiative taken up by EdJustice in the direction to bridge the gaps which exist due to differences in opportunities available to us.
                The aim of mentorship is to aid the student intellectually, emotionally and spiritually into becoming a better person. The mentor will work in the direction of making their mentee self sustainable, broad minded and and a confident person. Mentors try to instill awareness based understanding and the power of questioning in their mentees.<br />
                        <button className="program-ViewMoreButton" onClick={contentShow1}>Read More</button></p>
                </div>
            </div>
            <div className="program1-MoreContent">
                <h2>EdConnect- Person to Person Learning</h2><br />
                <p>The EdConnect Mentor-Mentee programme is our flag-ship programme which facilitates distance-learning and connect students with committed and educated mentors from all round the globe. We use internet and communication technology, available via mobile phones, to make quality education available to all students from government schools. The aim is to facilitate holistic growth of a student and to provide masses an opportunity to work for improving the condition of public education in India. The mentor interacts with the mentee virtually via phone call or video call to teach and guide him/her on a regular basis.</p>
                <br />
                <br />
                <h3>Programme Structure:</h3>
                    The programme structure focuses on a multi-faceted approach to incorporate both- personality development as well as academics. We follow a 60-40 approach where 60% focus is on soft skill development including confidence building, problem-solving and creative thinking. The later 40% is based on assisting academics and working towards short term learning goals. The mentor provides the mentee with regular guidance and help needed for him or her to achieve these goals.
                    <br /><br />We pair girl students with female mentors only. However boys can be mentored either males or females. The pairing process is guided by mentee’s array of interests and expectations and the experience and profile of the mentor.
                    <br />
                <br />
                <br />
                <h3>Who is a Mentor?</h3>
                <p>A mentor is any individual who is
                    <ul>
                        <li>Currently a 2nd year student of an undergraduate programme or possessing a higher educational qualification</li>
                        <li>Is willing to teach as well as guide their mentee regularly</li>
                        <li>Available to talk to his/her mentee over phone at least once a week</li>
                        <li>Is dedicated to share his/her knowledge and make a positive impact </li>
                    </ul>
                </p>
                <br />
                <br />
                <h3>Who is a Mentee?</h3>
                <p>A mentee is a student of government educational institution in Bihar above 14 years of age. These children often face the brunt of poor-quality education and are eager to learn more and achieve their high aims. Their interests and expectations steer the learning process.</p>
                <button className="program-applybutton">APPLY</button>
            </div>
        </>
    )
}

export default Program1;