import React from 'react';
import dbOperations from '../../db/crud';
class Contactform extends React.Component {
    constructor() {
        super();
        this.state = { msgtrue: '', msgfalse: '' };
    }
    saveContactInfo() {
        var object = {};
        var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        
        for (let key in this.refs) {
            object[key] = this.refs[key].value;
        }
        console.log('Contact : ',object);
        if (object.name == "" && object.email == "" && object.subject == "" && object.msg == "") {
            this.setState({ msgfalse: 'First fill out all fields Correctly!' });
        }
        else{
            if (object.email.match(mailformat)) {
                var pr = dbOperations.addContact(object);
                pr.then(data => {
                    this.setState({ msgtrue: 'Form Submitted', msgfalse: '' });
                }).catch(err => {
                    this.setState({ msgfalse: 'Some Error Occurred. Try Again!', msgtrue: '' });
                })
            }
            else{
                this.setState({msgfalse: 'Email is Not Valid!'})
            }
        }
    }


    render() {
        return (
            <div className="contact-div">
                <div className="contact-contactform">
                    <div className="contact-contactform-from">
                        <input type="text" ref="name" placeholder="Name" className="inputName" />&ensp;
                        <input type="text" ref="email" placeholder="Email" className="inputEmail" /><br />
                        <input type="text" ref="subject" placeholder="Subject" className="inputSubject" /><br />
                        <textarea rows="6" ref="msg" placeholder="Type your message here..." className="inputMsg" />
                        <div className="contact-submit-div">
                            <button className="contact-submit" onClick={this.saveContactInfo.bind(this)}>Submit</button>
                        </div>
                        <div className="contact-msg">
                            <p className="contact-msg-true">{this.state.msgtrue}</p>
                            <p className="contact-msg-false">{this.state.msgfalse}</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Contactform;
