import React from 'react';
import OurProgramGrid from './ourProgramGrid';
import '../../css/home/ourProgram.css';

function OurPrograms() {

    const programValues = [
        { programImage: 'assets/image/home/EdConnect-Mentoring.jpg', programName: 'EdConnect Mentoring' },
        { programImage: 'assets/image/home/gyaan-daan.jpg', programName: 'Gyaan Daan' },
        { programImage: 'assets/image/home/edconnect-virtual.jpg', programName: 'EdConnect Virtual' }
    ]

    return (
        <>
            <div className="ourPrograms">
                <h1 className="program-heading edjusticeColor">Our&nbsp;</h1><h1>Programs</h1>
            </div>
            <div className="ourProgramsGridCollection">
                {programValues.map((item, i) =>
                    <OurProgramGrid key={i} programName={item.programName} programImage={item.programImage} />
                )}
            </div>
        </>
    )
}

export default OurPrograms;