import React from 'react';
import '../../css/home/statsdata.css';

const StatsDataAnimation = (props)=> {
    const myStyle={
        background: `radial-gradient(black 57%, transparent 40%), conic-gradient(#e05b0d 0% ${props.donut}, transparent ${props.donut}  100%)`
    };
    return (
        <div className="statsdataanimation">
            <div className="circledata-statsDataAnimation">
                <div className="donut1" style={myStyle}></div>
                <div className="donut2"></div>
                <div className="donut-value">{props.percentage}</div>
            </div>
            <div className="donut-below-content">
            {props.content}
            </div>
        </div>
    )
}

export default StatsDataAnimation;
