import React from 'react';
import '../../css/about/about.css';

function SanjaySirIntro() {
    return (
        <div className="about-first">
            <div className="about-sanjaySir-text">
                <h1 className="about-sanjaySir-text-name">Dr. Sanjay Kumar</h1>
                <p className="about-sanjaySir-text-tag">-Founder EdJustice</p>
                <p className="about-sanjaySir-text-quote">"Public education can only be rejuvenated when people of all sections are involved. Edjustice People’s Campaign is an effort to mobilize every sections of the society to contribute their voluntary time to help schools and colleges that are educating about 70-80 per cent students of the country.​​​"</p>
            </div>
            <div className="about-sanjaySir-image">
                <img src="assets/image/about/sanjay-sir.jpg"></img>
            </div>
        </div>
    )
}

export default SanjaySirIntro;
