import React from 'react';
import dbOperations from '../../db/crud';
import '../../css/footer/getinvolved.css';

class NewsletterSubscribe extends React.Component {
    constructor() {
        super();
        this.state = { msgtrue: '', msgfalse: '' };
    }
    saveInfo() {
        var object = {};
        var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        for (let key in this.refs){
            object[key] = this.refs[key].value;
        }

        if(object.name=="" && object.email==""){
            this.setState({ msgfalse: 'First fill out all fields Correctly!' });
            document.getElementById("inputfield1").style.borderColor = "red";
            document.getElementById("inputfield2").style.borderColor = "red";   
        }
        else
        if(object.email.match(mailformat)){

            var pr = dbOperations.add(object);
            pr.then(data => {
                this.setState({ msgtrue: 'Thanks for Subscribing',msgfalse:'' });
            }).catch(err => {
                this.setState({ msgfalse: 'Some Error Occurred. Try Again!',msgtrue:'' });
            })
            document.getElementById("inputfield1").style.borderColor = "rgba(128, 128, 128, 0.685)";
            document.getElementById("inputfield2").style.borderColor = "rgba(128, 128, 128, 0.685)";
        }
    }
    render() {
        return (
            <div className="newsletterSubscribe">
                <div className="newslettersubscribe-heading">
                    <h1>Keep up to date with our progress!</h1>
                    <h2>Receive our monthly newsletter and many more updates on your email</h2>
                </div>
                <div className="newslettersubscribe-input">
                    <input id="inputfield1" className="subscribe-input-1" ref="name" type="text" placeholder="Name" required></input>
                    <input id="inputfield2" className="subscribe-input-2" ref="email" type="text" placeholder="Email" required></input>
                </div>
                <div className="newslettersubscribe-submit">
                    <button onClick={this.saveInfo.bind(this)}>Submit</button>
                </div>

                <div className="subscribe-msgtrue" >{this.state.msgtrue}</div>
                <div className="subscribe-msgfalse" >{this.state.msgfalse}</div>


                <div className="footer-contactinfo">
                    <h2>Contact us at : contact@edjustice.in</h2>
                </div>
            </div>
        )
    }

}

export default NewsletterSubscribe;
