import React from 'react';

const Program1 = () => {
    const contentShow3 = () => {
        var x3 = document.getElementsByClassName("program3-MoreContent")[0];
        var y3 = document.getElementsByClassName("program-program3")[0];

        if (x3.style.display == "none" || x3.style.display == "") {
            x3.style.display = "block";
            x3.scrollIntoView();
        } else {
            x3.style.display = "none";
            y3.scrollIntoView();
        }

    }
    return (
        <>
            <div className="program-program3">
                <div className="program3-image">
                    <img src="assets/image/program/program3.webp" />
                    <h2 className="program3-title">Edconnect Mentoring</h2>
                </div>
                <div className="program3-content">
                    <p>The Mentor- Mentee Challenge is an initiative taken up by EdJustice in the direction to bridge the gaps which exist due to differences in opportunities available to us.
                The aim of mentorship is to aid the student intellectually, emotionally and spiritually into becoming a better person. The mentor will work in the direction of making their mentee self sustainable, broad minded and and a confident person. Mentors try to instill awareness based understanding and the power of questioning in their mentees.<br />
                        <button className="program-ViewMoreButton" onClick={contentShow3}>Read More</button></p>
                </div>
            </div>
            <div className="program3-MoreContent">
                <h2>EVirtual Classes</h2><br />
                <p>Do you want to contribute to our mission but feel that we aren’t present in schools nearby you? If yes! that is absolutely not a problem. If you have a phone/laptop, a sound internet connection and knowledge to share, you can be a Digital Shiksha Saathi too.</p>
                <br />
                <br />
                <p>
                Our virtual class initiative is an amalgamation of technology with academia. It opens gateway for students in government schools of Bihar to interact with people from all around the globe and to learn via their experiences and learnings. Virtual classes are conducted by Digital Shiksha Saathis through mediums such as Skype, Google Hangouts and What’s App video calls. The classes are screened usually for a duration of sixty minutes wherein Saathis take up a wide collection of topics ranging from Photosynthesis to Career Decision Making. The sessions conducted are neither monotonous nor one-way dialogue. Students actively participate with their insights and questions. We are currently conducting virtual classes in Katihar, Poornia and Sahrsa.
                    </p><br /><br />Join us to lead these children into a better tomorrow. Its your chance to put your knowledge and skills for the betterment of the future of the nation.
                <br />
                <br />
                <button className="program-applybutton">APPLY</button>
            </div>
        </>
    )
}

export default Program1;