import React from 'react';
import dbOperations from '../../db/crud';

class Volunteer extends React.Component {
    constructor() {
        super();
        this.state = {
            modalvolunteer: document.getElementsByClassName("modalvolunteer"),
            msgfalsevolunteer: '',
            msgtruevolunteer: ''
        }
    }

    mybtnvolunteer = () => {
        console.log('mybtn');
        this.state.modalvolunteer[0].style.display = "block";
    }

    myspanvolunteer = () => {
        console.log('myspan');
        this.state.modalvolunteer[0].style.display = "none";
    }

    volunteerInfo() {
        console.log('volunteerInfo started');
        var object = {};
        var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        for (let key in this.refs) {
            object[key] = this.refs[key].value;
        }
        console.log('submit info : ', object);

        if(object.name=="" && object.email==""){
            this.setState({ msgfalsevolunteer: 'First fill out all fields Correctly!' });
            // document.getElementById("inputfield1").style.borderColor = "red";
            // document.getElementById("inputfield2").style.borderColor = "red";   
        }
        else{
            var pr = dbOperations.addVolunteer(object);
            pr.then(data => {
                this.setState({ msgtruevolunteer: 'Submit Successfull!',msgfalsevolunteer:'' });
            }).catch(err => {
                this.setState({ msgfalsevolunteer: 'Some Error Occurred. Try Again!',msgtruevolunteer:'' });
            })
            // document.getElementById("inputfield1").style.borderColor = "rgba(128, 128, 128, 0.685)";
            // document.getElementById("inputfield2").style.borderColor = "rgba(128, 128, 128, 0.685)";
        }
    }
    render() {
        return (
            <>
                <div className="support-volunteer">
                    <div className="support-volunteer-allcontent">
                        <div className="support-volunteer-image">
                            <img src="assets/image/support/volunteer.webp" />
                        </div>
                        <div className="support-volunteer-content">
                            <div>
                                <p className="edjusticeColor volunteer-title">Volunteer&nbsp;</p><p className="volunteer-title">with EdJustice </p>
                            </div>
                            <p>Through the Volunteer Programme, we provide individuals from the best universities and workplaces an opportunity to drive change in public education by building an ecosystem based on GYAN DAAN. Volunteers embark on a journey of learning and personal growth while contributing and creating change. Through a one year engagement with like-minded peers, gaining mentorship and training by leaders within the social sector and exposure to grassroot realities of the Public Education system, our volunteers cultivate knowledge, skills and leadership while contributing to the movement seeking quality education for all.</p>
                            <button id="myBtn" onClick={this.mybtnvolunteer}><span className="edjusticeColor">&#10148; </span>Apply Now</button>
                        </div>
                    </div>
                </div>


                <div id="myModal" className="modalvolunteer">

                    <div className="modal-content">
                        <div className="modal-header">
                            <h3 className="modal-heading">Volunteer Form</h3>
                            <span onClick={this.myspanvolunteer} className="close">&times;</span>
                        </div>
                        <div className="volunteer-formcollection">
                            <div className="volunteer-form">
                                <div>
                                    <input type="text" ref="name" placeholder="Full Name" required/><br />
                                    <input type="text" ref="email" placeholder="Email" required/><br />
                                    <input type="number" ref="mobnum" placeholder="Mobile Number" required/><br />
                                    <input type="text" ref="institute" placeholder="Current Institution" required/><br />
                                    <input type="text" ref="city" placeholder="City" required/><br />
                                    <select ref="education" placeholder="Highest level of education(as of January 2020)">
                                        <option value="highersecondarygraduate">Higher Secondary Graduate</option>
                                        <option value="graduate">Graduate</option>
                                        <option value="post-graduate">Post Graduate</option>
                                        <option value="PhD">Ph.D</option>
                                    </select><br />
                                    <select ref="stream" placeholder="Undergraduate Stream">
                                        <option value="arts">Arts And Social Sciences</option>
                                        <option value="commerce">Commerce</option>
                                        <option value="science">Science</option>
                                        <option value="engineering">Engineering</option>
                                        <option value="others">Others</option>
                                    </select><br />
                                    <select ref="currentProfession" placeholder="Current Professional Status">
                                        <option value="study">Studying full time</option>
                                        <option value="working">Working full time</option>
                                        <option value="neither">Neither studying nor working</option>
                                    </select><br />
                                    <select ref="whereHear" placeholder="Where did you hear about EdJustice People's Campaign?">
                                        <option value="massMedia">Mass Media ( Newspaper/ Magazine/ TV/ Radio, etc)</option>
                                        <option value="onlineMedia">Online Media (Social Media/ Websites/ Job Portals,etc.)</option>
                                        <option value="wordOfMouth">Word of mouth</option>
                                        <option value="other">Other</option>
                                    </select><br />

                                </div>
                                <div className="volunteer-form-submit">
                                    <button onClick={this.volunteerInfo.bind(this)}>Submit</button>
                                    <div>
                                        <p className="volunteer-form-submit-false">{this.state.msgfalsevolunteer}</p>
                                        <p className="volunteer-form-submit-true">{this.state.msgtruevolunteer}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }

}

export default Volunteer;