import React from 'react';

const Program2 = () => {
    const contentShow2 = () => {
        var x2 = document.getElementsByClassName("program2-MoreContent")[0];
        var y2 = document.getElementsByClassName("program-program2")[0];
        var x3 = document.getElementsByClassName("program3-MoreContent")[0];
        var y3 = document.getElementsByClassName("program-program3")[0];

        if (x2.style.display == "none" || x2.style.display == "") {
            x2.style.display = "block";
            x2.scrollIntoView();
        } else {
            x2.style.display = "none";
            y2.scrollIntoView();
        }
        // if (x3.style.display == "none" || x3.style.display == "") {
        //     x3.style.display = "block";
        //     x3.scrollIntoView();
        // } else {
        //     x3.style.display = "none";
        //     y3.scrollIntoView();
        // }



    }
    return (
        <>
            <div className="program-program2">
                <div className="program2-content">
                    <p>The Mentor- Mentee Challenge is an initiative taken up by EdJustice in the direction to bridge the gaps which exist due to differences in opportunities available to us.
                The aim of mentorship is to aid the student intellectually, emotionally and spiritually into becoming a better person. The mentor will work in the direction of making their mentee self sustainable, broad minded and and a confident person. Mentors try to instill awareness based understanding and the power of questioning in their mentees.<br />
                        <button className="program-ViewMoreButton" onClick={contentShow2}>Read More</button></p>
                </div>
                <div className="program2-image">
                    <img src="assets/image/program/program2.webp" />
                    <h2 className="program1-title">GYAAN DAAN</h2>
                </div>
            </div>
            <div className="program2-MoreContent">
                <h2>Gyaan Daan</h2><br />
                <br />
                <p>
                    We at EdJustice fervently believe in the power of sharing knowledge. Hence, we seek to mobilise a pool of 100 volunteers, who would be known as Shiksha Saathis, in every district of Bihar. These volunteers would carry forward this education revolution in their districts. They would visit the government schools associated to EdJustice and contribute by directly teaching students. The subject matter of teachings would be dependent on the expertise of the volunteer. For ex- A Software Engineer can teach students computer applications, a Banker can help students with basic maths and so on.
                </p><br/><br/><p>
                    The volunteers would also help to expand our network to more government schools in their district. The entry of common masses into schools would be also be major breakthrough as they would be exposed to the astounding on-ground situations which are completely opposite to a healthy learning environment. Such experiences, we believe, would create long lasting realisations amongst the people.
                    </p><br/><br/><p>
                    Thus, helping “betterment of education” to be established as a major agenda of the masses.
                    </p><br/><p>
                    The simple act of sharing knowledge can lead to biggest and brightest of changes.  Who knows  someone you are helping today might be the next Abdul Kalam or CV Raman. Possibilities  are many but action is the only way to success.
                </p>
                <br />
                <br />

                <h3>Join your District Team and be a Shiksha Saathi.</h3>
                <button className="program-applybutton">APPLY</button>
            </div>
        </>
    )
}

export default Program2;