import React from 'react';
import '../../css/about/about.css';

function BiharCampaign() {
    return (
        <div className="about-biharCampaign">
            <div className="about-biharCampaign-image">
                <img src="assets/image/about/bihar-campaign.jpg"></img>
                <p>The Campaign In Bihar</p>
            </div>
            <div className="about-biharCampaign-text">
                <p>Our campaign aims at systemic transformation and reformation in state-run educational institutions to make equity in the Public education system a reality through a model based on the principle of Individual Social Responsibility.</p>
            </div>
        </div>
    )
}

export default BiharCampaign;
