import React from 'react';
import { NavLink } from 'react-router-dom';
import '../css/common/navbar.css';

const Navbar = (props) => {
  
    return (
        <div className="navbar">
            <p className="navbar-close" onClick={props.close}>&#10006;</p>
            <div className="navbar-links">
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/">Home</NavLink>
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/about">About</NavLink>
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/jan-shikhsha-yatra">Jan Shikhsha Yatra</NavLink>
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/programs">Programs</NavLink>
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/support-us">Support Us</NavLink>
                <NavLink activeClassName="active-navlink" onClick={props.close} to="/contact-us">Contact Us</NavLink>
            </div>
        </div>
    )
}

export default Navbar;