import React from 'react';
import '../../css/home/statsdata.css';
import StatsDataAnimation from './statsDataAnimation';


function StatsData() {

    const statsDatadonut = [
        { donut: '50%', percentage: '50%', content: 'Less than half of India’s children between the age 6 and 14 go to school.' },
        { donut: '40%', percentage: '40%', content: 'Professional qualified teachers at higher secondary level' },
        { donut: '20%', percentage: '20%', content: 'In India, only 20% of habitation has a secondary school.' },
        { donut: '60%', percentage: '60%', content: 'In nearly 60% of schools, there are less than two teachers to teach' }
    ];
    return (
        <div className="statsdata">
            <div className="heading-statsdata">
                <div className="headingcontainer-statsdata">
                    <p className="heading1-statsdata">1 in 3</p>
                    <p className="heading2-statsdata">Children around the world lack access to a quality basic education</p>
                </div>
            </div>
            <div className="stats-statsdata">
                {statsDatadonut.map((item, i) =>
                    <StatsDataAnimation className="stats-statsdatadonut" key={i} donut={item.donut} percentage={item.percentage} content={item.content} />
                )}
            </div>
        </div >
    )
}

export default StatsData;
