import React from 'react';
import HomePage from './container/homePage';
import './app.css';
import Navbar from './container/navbar';
import { Switch, Route } from 'react-router-dom';
import About from './container/about';
import JanShikhshaYatra from './container/janShikhshaYatra';
import Programs from './container/programs';
import SupportUs from './container/supportUs';
import ContactUs from './container/contactUs';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      navbarOpenClose: false
    }
  }

  navbarClose = () => {
    this.setState({ navbarOpenClose : !this.state.navbarOpenClose });
  }

  render() {
    return (
      <>
        <div className="fixed-elements">
          <div className="fixed-socialbar">
            <a href="https://www.facebook.com/edjusticeindia"><img src="assets/image/common/facebook.png"></img></a>
            <a href="https://www.twitter.com/edjusticeindia"><img src="assets/image/common/twitter.png"></img></a>
            <a href="https://www.youtube.com/channel/UCzmJdPZ1ACkBGUB6rGezqlQ"><img src="assets/image/common/youtube.png"></img></a>
            <a href="https://www.instagram.com/edjusticeindia"><img src="assets/image/common/insta.png"></img></a>
          </div>
          <a href="#"><div className="fixed-donate">
            <p className="fixed-donate-heart">&#10084;&nbsp;</p><p>Donate</p>
          </div></a>
          <div className="fixed-edjustice-logo">
            <a href="#"><img src="assets/image/common/logo.png" /></a>
          </div>
          <div className="fixed-navbutton">


            {this.state.navbarOpenClose ?
              <Navbar close={this.navbarClose} />
              : 
              <div className="nav-burger">
                <img src="assets/image/common/navbar-burger.png" onClick={this.navbarClose} />
              </div>
            }
            <Switch>
              <Route exact path="/" component={HomePage} />
              <Route path="/about" component={About} />
              <Route path="/jan-shikhsha-yatra" component={JanShikhshaYatra} />
              <Route path="/programs" component={Programs} />
              <Route path="/support-us" component={SupportUs} />
              <Route path="/contact-us" component={ContactUs} />
            </Switch>
          </div>
        </div>
      </>
    );
  }
}

export default App;
