import React from 'react';

function Vision() {
    return (
        <div>
            <div className="about-vision">
                <div className="about-vision-text">
                    <p>The Campaign aims to make good quality education available and accessible for all by strengthening public-funded educational institutions with active engagement of different stakeholders in the society including the government.</p>
                </div>
                <div className="about-vision-image">
                    <img src="assets/image/about/vision.jpg"></img>
                    <p>Vision</p>
                </div>
            </div>
        </div>
    )
}

export default Vision;
