import React from 'react';

const OurProgramGrid = (props) => {
    return (
            <div className="programSingleGrid">
                <img className="programSingleGrid-img" src={`${props.programImage}`}/>
                <div className="programName-heading">
                    <h2>{props.programName}</h2>
                </div>
                <div className="program-content-learnmore">
                    <p>Learn More</p>
                </div>
            </div>
    );
}

export default OurProgramGrid;
