import React from 'react';
import '../../css/home/workDone.css';

function WorkDone() {
    return (
        <div className="workDone">
            <div className="workDone-path">
                <img src="assets/image/home/workDone.png"></img>
            </div>
            <div className="workDone-content">
                <div className="workDone-content-1">
                    <p className="edjusticeColor workDone-content-heading counter">1600+</p>
                    <p className="workDone-content-data">Students empowered through workshop program</p>
                </div>
                <div className="workDone-content-2">
                    <p className="edjusticeColor workDone-content-heading counter">1635+</p>
                    <p className="workDone-content-data">Hours of on ground work</p>
                </div>
                <div className="workDone-content-3">
                    <p className="edjusticeColor workDone-content-heading counter">150+</p>
                    <p className="workDone-content-data">Supporters mobilized from all over the world</p>
                </div>
                <div className="workDone-content-4">
                    <p className="edjusticeColor workDone-content-heading counter">1590+</p>
                    <p className="workDone-content-data">KM of distance covered</p>
                </div>
            </div>
        </div>
    )
}

export default WorkDone;
