import React from 'react';
import '../../css/about/about.css';

function Mission() {
    return (
        <div className="about-mission">
            <div className="about-mission-image">
                <img src="assets/image/about/mission.jpg"></img>
                <p>Mission</p>
            </div>
            <div className="about-mission-text">
                <p>EdJustice is a people’s campaign to rejuvenate state run educational institutions with the objective of increasing social stake holding and mobilizing social and emotional resources for enhancing the overall personality development of the students to achieve  intra- generational mobility. The purpose of the Campaign is to strengthen the government efforts with a clear understanding that state education is a collective responsibility of the government and the society. </p>
            </div>
        </div>
    )
}

export default Mission;