import React from 'react';
import '../../css/home/edjusticeintro.css';

function EdjusticeIntro() {
    return (
        <div className="edjusticeintro">
            <div className="heading">
                <h2 className="edjustice-edjusticeintro edjusticeColor">EdJustice</h2>
                <h2 className="tagline-edjusticeintro">People's Campaign</h2>
            </div>
            <div className="introcontent">
            Our campaign aims at a systemic transformation and reformation in state-run educational institutions to rejuvenate the public education system through a model based on the principle of ‘Individual Social Responsibility’.
            Following a model based on 'Gyaan Daan', we aim to equip students with information, knowledge and skills to enable them to unlock their potential and create a path to achieve intragenerational mobility. 
            </div>
            <button className="viewmore-edjusticeintro">View More</button>
        </div>
    )
}

export default EdjusticeIntro;
